<template>
    <div style="height: 7%"></div>
    <div style="background-color: aliceblue; height: 100%;overflow-y: scroll;">
      <div style="margin-top:1%;">
        <span class="smalltext">NAME:</span><span class="bigtext">{{ name0 }}</span>
        <span class="smalltext">FROM:</span><span class="bigtext">{{ fromSBOM }}</span>
        <span class="smalltext">TO:</span><span class="bigtext">{{ toSBOM }}</span>
        <span class="smalltext">TIME:</span><span class="bigtext">{{ time0 }}</span>
      </div>
      <!-- <h1 style="margin-top:1%" >比较报告</h1> -->
      <div class="demo-collapse">
        <el-collapse v-model="activeNames" @change="handleChange" >
          <el-collapse-item v-for="(value, key,index) in total_counts" :title='`${key}[${value}]`' :name="index">
            <div><p v-for="(value0, key0,index0) in detail[key]" style="Text-algin:left;margin:1%">
              <p style="font-weight:bold">{{index0 }}</p>
              <p v-if="index0||index0==0">{{ key0 }}</p>
              <p>{{ value0 }}</p>
            </p></div>
          </el-collapse-item>
        </el-collapse>
      </div>
      
      <div v-for="(value1, key1,index1) in detail" >
        <Tree v-if="index1>2"  :dependency="value1" :name="key1" />
      </div>
      
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  import Tree from '@/components/graphs/tree.vue'
  const echarts = require('echarts')
  export default {
    components: {
      Tree,
    },
    data() {
        return{
          name0:"dubbo",
          fromSBOM:"SPDX",
          toSBOM:"GEEKSBOM",
          time0:"2023-8-15",
          
    total_counts:{
        "component_samedict": 518,
        "component_diffdict_osschain": 110,
        "component_diffdict_comparedtools": 2,
        "dependency_samesrc_sametag": 1039,
        "dependency_samesrc_difftag_osschain": 875,
        "dependency_samesrc_difftag_comparedtools": 128,
        "dependency_diffsrc_difftag_osschain": 337,
        "dependency_diffsrc_difftag_comparedtools": 241
    },
    detail:{
      "component_samedict": [],
      "component_diffdict_osschain": [],
      "component_diffdict_comparedtools": [],
      "dependency_samesrc_sametag": [],
      "dependency_samesrc_difftag_osschain": [],
      "dependency_samesrc_difftag_comparedtools":[],
      "dependency_diffsrc_difftag_osschain":[],
      "dependency_diffsrc_difftag_comparedtools": []
    }
  }
},
    mounted() {
      var _this=this;
      axios.get('/flare0.json').then(res => {     // 获取public下的test.json文件数据
        // console.log(res.data);
        let components=res.data.components;
        let dependencies=res.data.dependencies;
        _this.detail.component_samedict=components.component_samedict;
        _this.detail.component_diffdict_osschain=components.component_diffdict_osschain;
        _this.detail.component_diffdict_comparedtools=components.component_diffdict_comparedtools;
        _this.detail.dependency_samesrc_sametag=dependencies.dependency_samesrc_sametag;
        _this.detail.dependency_samesrc_difftag_osschain=dependencies.dependency_samesrc_difftag_osschain;
        _this.detail.dependency_samesrc_difftag_comparedtools=dependencies.dependency_samesrc_difftag_comparedtools;
        _this.detail.dependency_diffsrc_difftag_osschain=dependencies.dependency_diffsrc_difftag_osschain;
        _this.detail.dependency_diffsrc_difftag_comparedtools=dependencies.dependency_diffsrc_difftag_comparedtools;
        // console.log(_this.detail);
      })
    },
    
  }
  </script>
  
  <style>
.demo-collapse{
  margin:3%;
  margin-top:1%;
  text-align: left;
}
#tree{
  margin:3%;
}
.smalltext{
  font-family: "Lucida Calligraphy",ursive, serif, sans-serif;
  font-weight: 200;
  margin-right:20px;
  margin-left:80px;
}
.bigtext{
  font-weight: 1000;
  margin-right:100px;
  font-size:x-large;
}
  </style>
  