<template>
    <h4>{{ name }}
    <el-button type="primary" style="margin-left: 16px" @click="drawer = true"> open </el-button>
      <!-- <el-drawer v-model="drawer" :title="name" :with-header="false"><span style="Text-algin:left;font-weight: lighter;">{{ dependency }}</span></el-drawer> -->
      <el-drawer v-model="drawer" :title="name" :with-header="false">
        <p v-for="(value,key,index) in dependency" >
            <p style="font-weight: bold;" align="left" >{{index}}</p>
            <p style="font-weight: bold;" align="left" >{{key}}</p>
            <p v-if="dependency[key]">
                <p v-for="value2 in value" style="Text-algin:left;font-weight: lighter;" align="left">{{ value2 }}</p>
            </p>
        </p>
    </el-drawer>

    </h4>
      <div id="tree" style="height: 80vh;width: 95%;background-color: aliceblue;margin:1%"  ref="echartstree"></div>
  </template>
  
  <script>
  import * as echarts from 'echarts';
  export default {
    data() {
        return{
            data0:[],
            drawer:false,
        }
    },
    props: ["dependency","name"],
    watch:{
        dependency:function(newData,oldData){
				// console.log(newData)  //newData就是orderData
				this.data0 = newData; 
                this.draw();    
		}

    },
    methods:{
        draw() {
        var _this=this;
        var myChart = echarts.init(_this.$refs.echartstree, null, {
        renderer: 'canvas',
        useDirtyRect: false
        });
        var option;
        let data1=this.data0;
        let data={
            "name":_this.name,
            "children":[]
        }
        // console.log(this.data0)
        for (let key in data1){
            if(data1[key]){
                let arr=[]
                for (let j in data1[key]){
                    arr.push({"name":data1[key][j]});
                }
                data.children.push({"name":key,"children":arr});
            }else{
                //空的
                data.children.push({"name":key});

            }
        }
        // console.log(data);
        myChart.showLoading();
// d3.json('../flare.json',function (data) {
  myChart.hideLoading();
  data.children.forEach(function (datum, index) {
    index % 2 === 0 && (datum.collapsed = true);
  });
  myChart.setOption(
    (option = {
      tooltip: {
        trigger: 'item',
        triggerOn: 'mousemove',
        feature: {saveAsImage: {show: false, title: '保存截图', type: 'png'}}
 
    },
      series: [
        {
          type: 'tree',
          //11
        //   layout: 'force',
            roam: true,//移动+放大
 			// roam: 'scale',// 放大
 			// roam: 'move',// 移动
          data: [data],
          top: '1%',
          left: '7%',
          bottom: '1%',
          right: '20%',
          symbolSize: 7,
          label: {
            position: 'left',
            verticalAlign: 'middle',
            align: 'right',
            fontSize: 9
          },
          leaves: {
            label: {
              position: 'right',
              verticalAlign: 'middle',
              align: 'left'
            }
          },
          emphasis: {
            focus: 'descendant'
          },
          expandAndCollapse: true,
          animationDuration: 550,
          animationDurationUpdate: 750
        }],
        // dataset: { source: [ ['product', 'parent', 'value'], ['根节点', null, 0], ['子节点 1', '根节点', 10], ['子节点 2', '根节点', 20], ['子节点 1.1', '子节点 1', 30], ['子节点 1.2', '子节点 1', 40] ] }, 
        zoom: 100
        // 缩放级别 
    })
  );
// });

    if (option && typeof option === 'object') {
      myChart.setOption(option);
    }


    }}
}
  </script>
  
  <style>
.demo-collapse{
  margin:3%;
  text-align: left;
}
#tree{
  margin:3%;
}
  </style>
  